
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.popup {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content:center;

  color: #FFFFFF;
  font-size: 16px;
  width: 160px;
  height: 60px;
  background: #222222;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  opacity: 0.6;
}
.index {
  color: #1890ff;
}

.headTop {
  position: fixed;
  top: 0;
  left: 0;
  line-height: 1.066667rem;
  height: 1.066667rem;
  width: 100%;
  font-size: 0.533333rem;
  color: #3b3b3b;
  border-bottom: 0.053333rem solid #FFFFFF;
  background: #d9d9d9;
  z-index: 100;
}
.headTop .title {
  position: relative;
  text-align: center;
}
.headTop .arrow {
  position: absolute;
  top: 0.32rem;
  left: 0.32rem;
  display: inline-block;
  border-top: 0.053333rem solid #BBBBBB;
  border-right: 0.053333rem solid #BBBBBB;
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
  width: 0.32rem;
  height: 0.32rem;
  z-index: 101;
}

.foot{
  background:#2D3339;
  display: flex;
  justify-content: center;
  align-items:center;
  flex-direction: column;
}
.copyright{
margin:30px;
height: 80px;
line-height: 42px;
color: #999;
font-size: 24px;
text-align: center;

}
.company-page {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  background: #d9d9d9;
}
.company-page .content {
  margin-top: 1.066667rem;
}
.company-page .input {
  margin-top: 1.066667rem;
  width: 8rem;
  height: 1.066667rem;
  border: 0.026667rem solid #000000;
  font-size: 0.48rem;
}
.company-page .button {
  margin-top: 0.8rem;
  width: 8rem;
  height: 1.333333rem;
  background: #40a9ff;
  color: #FFFFFF;
  font-size: 0.426667rem;
  text-align: center;
  line-height: 1.333333rem;
  border: 0.026667rem solid #DDDDDD;
  box-sizing: border-box;
  border-radius: 0.213333rem;
}
.company-page .content {
  margin-top: 1.066667rem;
  font-size: 0.373333rem;
  text-align: center;
}
.company-page .logo-img {
  margin-top: 2.133333rem;
  height: 5.333333rem;
  width: 5.333333rem;
}

.complaints {
  background: #FFFFFF;
}
