.headTop {
  position: fixed;
  top: 0;
  left: 0;
  line-height: 1.066667rem;
  height: 1.066667rem;
  width: 100%;
  font-size: 0.533333rem;
  color: #3b3b3b;
  border-bottom: 0.053333rem solid #FFFFFF;
  background: #d9d9d9;
  z-index: 100;
}
.headTop .title {
  position: relative;
  text-align: center;
}
.headTop .arrow {
  position: absolute;
  top: 0.32rem;
  left: 0.32rem;
  display: inline-block;
  border-top: 0.053333rem solid #BBBBBB;
  border-right: 0.053333rem solid #BBBBBB;
  transform: rotate(-135deg);
  width: 0.32rem;
  height: 0.32rem;
  z-index: 101;
}
