.company-page {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  width: 100%;
  background: #d9d9d9;
}
.company-page .content {
  margin-top: 1.066667rem;
}
.company-page .input {
  margin-top: 1.066667rem;
  width: 8rem;
  height: 1.066667rem;
  border: 0.026667rem solid #000000;
  font-size: 0.48rem;
}
.company-page .button {
  margin-top: 0.8rem;
  width: 8rem;
  height: 1.333333rem;
  background: #40a9ff;
  color: #FFFFFF;
  font-size: 0.426667rem;
  text-align: center;
  line-height: 1.333333rem;
  border: 0.026667rem solid #DDDDDD;
  box-sizing: border-box;
  border-radius: 0.213333rem;
}
.company-page .content {
  margin-top: 1.066667rem;
  font-size: 0.373333rem;
  text-align: center;
}
.company-page .logo-img {
  margin-top: 2.133333rem;
  height: 5.333333rem;
  width: 5.333333rem;
}
