.complaints .content .top {
  padding: 0.266667rem 0.266667rem;
}
.complaints .content .top .top-title {
  color: #000000;
  font-size: 0.533333rem;
  font-weight: bold;
}
.complaints .content .top .top-content {
  margin-top: 0.213333rem;
  height: 0.586667rem;
  font-size: 0.373333rem;
  line-height: 0.586667rem;
}
.complaints .content .top .top-content .periods {
  display: inline-block;
  color: #FFFFFF;
  width: 2.933333rem;
  text-align: center;
  background: #40a9ff;
}
.complaints .content .top .top-content .topProgram {
  font-size: 0.373333rem;
  display: inline-block;
  margin-left: 0.266667rem;
  width: 2.133333rem;
  color: #40a9ff;
  text-align: center;
  border: 0.026667rem solid #40a9ff;
  box-sizing: border-box;
}
.complaints .content .item {
  margin-bottom: 0.533333rem;
  padding: 0 0.266667rem;
  border-bottom: 0.026667rem solid #DDDDDD;
}
.complaints .content .avator {
  width: 1.066667rem;
  height: 1.066667rem;
}
.complaints .content .user-text {
  margin-left: 0.266667rem;
  margin-top: 0.8rem;
}
.complaints .content .content {
  margin-top: 0.266667rem;
  padding: 0.266667rem;
  position: relative;
  background: #ECECEC;
  border-radius: 0.133333rem;
}
.complaints .content .content-icon {
  position: absolute;
  top: -0.4rem;
  left: 0.266667rem;
  border-width: 0.213333rem;
  border-style: solid;
  border-color: transparent transparent #ECECEC transparent;
}
.complaints .content .images {
  padding: 0.133333rem 0;
}
.complaints .content .image {
  display: inline-block;
  margin-top: 0.133333rem;
  margin-right: 0.133333rem;
  height: 2.906667rem;
  width: 2.906667rem;
  overflow: hidden;
  border: 0.053333rem solid #DDDDDD;
}
.complaints .content .img {
  height: 100%;
  width: 100%;
}
.complaints .content .reply {
  margin-bottom: 0.4rem;
  padding: 0.4rem;
  position: relative;
  color: #FFFFFF;
  background: #23A6DD;
  border-radius: 0.266667rem;
  font-family: Microsoft Yahei;
  border: 0.026667rem solid rgba(255, 255, 255, 0);
}
.complaints .content .reply-hr {
  display: flex;
  justify-content: space-between;
}
.complaints .content .reply-content {
  padding-top: 0.133333rem;
}
